.Loader {

    border:16px solid #f3f3f3;
    border-top:16px solid rgb(92, 146, 169);
    width:120px;
    height:120px;
    border-radius: 50%;
    animation:spin 2s linear infinite;
    margin: auto;

}

@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}